<template>
  <div class="body">
    <!---->
    <div class="head" style="text-align: left;font-weight: 700;font-size: 16px;line-height: 50px;">&nbsp;&nbsp;&nbsp;&nbsp;</div>
    <!---->
    <div class="bian">
      <!---->
      <div class="selec">
        <el-select v-model="value" style="width: 500px" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <!---->
      <div class="bj">
        <div class="font"></div>
      </div>
      <!---->
      <div class="txt">
        <el-input
          type="textarea"
          style="height: 150px"
          :rows="6"
          placeholder=""
          v-model="textarea">
        </el-input>
      </div>
      <!---->
      <div style="text-align: left; margin: 20px 0px 0px 50px;">
        <el-button style="background-color:rgba(0, 153, 153, 1);color: white; "></el-button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        textarea: '',
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        value: ''
      }
    },
    methods: {
      edit() {
        this.centerDialogVisible = true;
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }
    }
  }
</script>

<style scoped>
  .bian {
    height: 600px;
    width: 99.90%;
    border: 1px solid #ddd;
  }
  .selec{
    margin:50px 0px 0px 50px;
    text-align: left;
  }
  .bj{
    width: 31%;
    height: 200px;
    margin: 20px 0px 0px 50px;
    background-color: rgba(242, 242, 242, 1);
  }
  .font{
    text-align: left;
    margin: 20px 0px 0px 20px;
    color: #333;
  }
  .txt{
    width: 31%;
    height: 150px;
    margin: 20px 0px 0px 50px;
  }
</style>



