<script>
  export default {
    data() {
      return {
        tableData: [],
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        value: '',
        //  
        dealer: true,
        members: false,
        textarea: '',
        checked2: '',
        orderInfo: {},
      }
    },
    methods: {
      //  
      getInfo() {
        this._getData('/api/v1/order/show', {
          id: sessionStorage.getItem('orderId'),
        }, data => {
          console.log(data)
          this.orderInfo = data;
        })
      }
    },
    created() {
      this.getInfo();
    }
  }
</script>

