    width: 100%;
    margin: 20px 0px 0px 20px;
  }
  
  .fonts {
    width: 20%;
    font-size: 14px;
    margin: 0px 0px 0px 10px;
  }
  
  /**/
  .pfont {
    text-align: left;
    font-size: 13px;
    line-height: 30px;
  }
  
  .pian {
    margin-left: 50px;
  }
  
  .content {
    width: 95%;
    border: 1px solid #ddd;
    margin: 10px 0px 30px 30px;
  }
  
  .cfont {
    margin: 10px 0px 0px 5px;
    font-size: 14px;
    width: 10%;
  }
  
  .ckuan {
    text-align: left;
    width: 85%;
  }
  
  .imgs {
    width: 20px;
    height: 20px;
    margin: 10px 0px 0px 20px;
  }
  
  .show {
