<template>
  <div class="body">
    <!---->
    <div class="flex whiteT">
      <div class="font"></div>
      <div class="input2">
        <el-input v-model="input" style="margin-top: 10px;width: 180px"  placeholder=""></el-input>
      </div>
      
      <div class="font"></div>
      <div class="input2">
        <el-select v-model="value"  style="margin-top: 10px;width: 180px" placeholder="">
          <el-option
            v-for="item in options"
            size="small"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      
      <div class="font"></div>
      <div class="input">
        <el-date-picker
          v-model="value6"
          style="width: 220px"
          type="datetimerange"
          start-placeholder=""
          end-placeholder=""
          :default-time="['12:00:00']">
        </el-date-picker>
      </div>
      
      <!---->
      <div class="btn" style="margin-left: 10%">
        <el-button type="primary" icon="el-icon-search"></el-button>
      </div>
      <div class="btn2">
        <el-button class="buttons3" icon="el-icon-refresh"></el-button>
      </div>
    </div>
    <!---->
    <div class="head right">
      <div class="head1"></div>
      <div class="head1"></div>
    </div>
    <div>
      <el-table
        ref="multipleTable"
        :data="tableData3"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        border>
        <el-table-column
          type="index"
          label=""
          align="center"
          width="70">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          min-width="170">
          <template slot-scope="scope">{{ scope.row.date }}</template>
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align="center"
          min-width="162">
        </el-table-column>
        <el-table-column
          prop="address"
          min-width="120"
          align="center"
          label=""
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align="center"
          min-width="300">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align="center"
          min-width="120">
        </el-table-column>
      </el-table>
    </div>
    <div class="pag">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[20, 50, 100]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total=totals>
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data(){
      return{
        input:'',
        value1:'',
        value6:'',
        //
        page: 1,
        pageSize: 20,
        currentPage4: 1,
        totals: 20,
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        value: '',
        tableData3: [{
          date: '2016-05-03',
          name: '',
          address: ''
        }],
        inputs2:'',
        multipleSelection: []
      }
    },
    methods: {
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //
      handleSizeChange(val) {
        this.pageSize = val;
        this.getService();
      },
      //
      handleCurrentChange(val) {
        this.page = val;
        this.getService();
      },
    }
  }
</script>

<style scoped>
</style>



