      <div class="state flex">
        <div class="state2"></div>
        <div class="state3">(1000)</div>
      </div>
      <div class="state flex">
        <div class="state2"></div>
        <div class="state3">(1000)</div>
      </div>
    </div>
    <!---->
    <div class="flex whiteT">
      <div class="font"></div>
      <div class="input">
        <el-input v-model="input" placeholder=""></el-input>
      </div>
      
      <div class="font"></div>
      <div class="input">
        <el-input v-model="input" placeholder=""></el-input>
      </div>
      
