        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[20, 50, 100]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total=totals>
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        input: '',
        value1: '',
        value6: '',
        //
        page: 1,
        pageSize: 20,
        currentPage4: 1,
        totals: 20,
        
        tableData3: [{
          date: '2016-05-03',
          name: '',
          address: ''
        }],
        multipleSelection: []
      }
    },
    methods: {
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //
      handleSizeChange(val) {
        this.pageSize = val;
        this.getService();
      },
      //
      handleCurrentChange(val) {
        this.page = val;
        this.getService();
      },
      //  
      look() {
        this.$router.push({name: 'RefundInfo'})
      }
    }
  }
</script>

<style scoped>
</style>



