<template>
  <div class="body">
    <!---->
    <div class="spaces whiteT kuan">
      <div style="width: 80%">
        <div class="font1"></div>
        <div class="flex">
          <div class="bian"></div>
          <div class="bian"></div>
          <div class="bian"></div>
          <div class="bian"></div>
          <div class="bian"></div>
          <div class="bian"></div>
        </div>
      </div>
      <router-link to="/order/orderList">
        <div class="bian" style="margin: 40px 20px 0px 0px"></div>
      </router-link>
    </div>
    <!---->
    <!---->
    <div class="flex" style="margin-top: 30px;">
      <div :class="[ 'state','flex', dealer==true?'bj':'']" @click="dealerInfo()">
        <div class="state2"></div>
      </div>
      <div :class="[ 'state','flex', members==true?'bj':'']" @click="member()" style="margin-left: 0px">
        <div class="state2"></div>
      </div>
    </div>
    <!---->
    <div v-if="dealer==true">
      <orderInfo ref="headerChild"></orderInfo>
    </div>
    <!---->
    <div v-else>
      <div class="head">
        <div class="backfont"> </div>
      </div>
      <div class="flex xiu" style="height: 140px;width: 100%">
        <div class="sfont">
          {{this.$refs.headerChild.orderInfo.address}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import orderInfo from '@/components/order/orderInfo'
  
  export default {
    data() {
      return {
        tableData: [{
          date: '2016-05-02',
          name: '',
          address: ''
        }],
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        value: '',
        //  
        dealer: true,
        members: false
      }
    },
    components: {
      orderInfo
    },
    methods: {
      //
      dealerInfo() {
        this.dealer = true;
        this.members = false;
      },
      //  
      member() {
        this.dealer = false;
        this.members = true;
      }
    }
  }
</script>

<style scoped>
  .kuan {
    background-color: rgba(255, 204, 204, 0.498039215686275);
    height: 110px;
    border: 1px solid rgba(255, 204, 204, 1);
  }
  
  .font1 {
    text-align: left;
    margin: 20px 0px 0px 30px;
    font-size: 16px;
    color: black;
    font-weight: 600;
  }
  
  .bian {
    text-align: left;
    width: 100px;
    height: 30px;
    border: 1px solid black;
    font-size: 15px;
    text-align: center;
    line-height: 30px;
    margin: 20px 0px 0px 30px;
    background-color: rgba(255, 255, 255, 1);
  }
  
  .sfont {
    margin: 20px 0px 0px 20px;
    text-align: left;
    font-size: 15px;
  }
</style>



