<script>
  import AliImg from '@/components/upload-ali'
  import videos from '@/components/video'
  
  export default {
    name: "xiugai",
    data() {
      return {
        video: '',
        listImg: '',
        tupian: [],
        detailsImg: '',
        yiji: '',
        erjiList: [],
        goodsForm1: {
          goods_groupId: '',
          goods_name: '',
          goods_label: ''
        },
        rules: {
          goods_groupId: [
            {required: true, message: '', trigger: 'blur'},
          ],
          goods_name: [
            {required: true, message: '', trigger: 'blur'},
          ]
        },
        rules1: {},
        select: 'ML',
        goodsForm2: {
          genre1: "",
          genre2: "",
          genre3: "",
          genre7: "",
          genre5: ""
        },
        rules2: {},
        goodsForm3: {
          genre1: "",
          genre2: "",
          genre3: "",
          genre7: "",
          genre5: "",
          genre6: ''
        },
        rules3: {},
        goodsForm4: {
          genre8: ""
        },
        rules4: {},
        goodsForm5: {},
        rules5: {
          member_price: [
            {required: true, message: '', trigger: 'blur'},
          ],
          original_price: [
            {required: true, message: '', trigger: 'blur'},
          ]
        },
        goodsForm6: {
          label: "",
          is_dealer: null
        },
        videos: false
      }
    },
    components: {
      AliImg,
      videos
    },
    mounted() {
      this.requireDetal(this.goods);
      // this.dialog(this.dialogVisible)
    },
    watch: {
      goods(goods) {
        this.requireDetal(goods)
      }
    },
    methods: {
      requireDetal(g) {
        this._getData('/api/v1/goods/show', {
          id: g.id
        }, goods => {
          console.log(goods, '!!')
          this.yiji = goods.goods_group.group_name;
          this.getErji(this.yiji)
          if (this.yiji == '') {
            this.goodsForm2.genre1 = goods.genre1;
            this.goodsForm2.genre2 = goods.genre2;
            let hl = goods.genre5.substring(goods.genre5.length - 2, goods.genre5.length);
            if (hl === "ML") {
              this.select = "ML";
              this.goodsForm2.genre5 = goods.genre5.substring(0, goods.genre5.length - 2)
            } else {
              this.select = "L";
              this.goodsForm2.genre5 = goods.genre5.substring(0, goods.genre5.length - 1);
            }
            this.goodsForm2.genre7 = goods.genre7;
          } else if (this.yiji == '') {
            this.goodsForm3.genre1 = goods.genre1;
            this.goodsForm3.genre2 = goods.genre2;
            this.goodsForm3.genre3 = goods.genre3;
            let hl = goods.genre5.substring(goods.genre5.length - 2, goods.genre5.length);
            if (hl === "ML") {
              this.select = "ML";
              this.goodsForm3.genre5 = goods.genre5.substring(0, goods.genre5.length - 2)
            } else {
              this.select = "L";
              this.goodsForm3.genre5 = goods.genre5.substring(0, goods.genre5.length - 1);
            }
            this.goodsForm3.genre6 = goods.genre6;
            this.goodsForm3.genre7 = goods.genre7;
          } else if (this.yiji == '') {
            this.goodsForm4.genre8 = goods.genre8;
          }
          this.goodsForm1.goods_name = goods.goods_name || "";
          this.goodsForm1.goods_label = goods.goods_label || "";
          this.tupian = goods.goods_img.split(',');
          this.listImg = goods.list_img || "";
          this.video = goods.video || "";
          this.detailsImg = goods.details_img || "";
          this.goodsForm6.warn_num = goods.warn_num || "";
          this.goodsForm6.is_dealer = goods.is_dealer === 1 ? true : false;
          this.goodsForm6.dealer_num = goods.dealer_num || "";
          this.goodsForm6.buyBack_num = goods.buyBack_num || "";
          this.goodsForm6.label = goods.label || "";
          this.goodsForm6.salesVolume = goods.salesVolume || "";
          this.goodsForm6.num = goods.num || "";
          this.goodsForm6.integral = goods.integral || "";
          this.goodsForm6.member_price = goods.member_price || "";
          this.goodsForm6.trade_price = goods.trade_price || "";
          this.goodsForm6.original_price = goods.original_price || "";
          this.goodsForm6.dealer_warn_num = goods.dealer_warn_num || "";
        })
      },
      getErji(option) {
        this._getData('/api/v1/goods_group/getSub', {
          group_name: option
        }, data => {
          this.erjiList = data;
          this.goodsForm1.goods_groupId = data.length === 0 ? '' : data[0].id;
        })
      },
      submitForm() {
        this.$refs.goodsForm1.validate((valid) => {
          if (valid) {
            this.$refs.goodsForm6.validate((valid6) => {
              if (valid6) {
                let imgArr = [];
                let subObj = new Object();
                for (let i = 1; i <= 5; i++) {
                  let img = this.$refs['jiuImg' + i].fileArr;
                  if (img.length !== 0) {
                    imgArr.push(img[0])
                  }
                }
                let lbImg = this.$refs.liebiao.fileArr;
                let spImg = this.$refs.shipin.fileArr;
                let xqImg = this.$refs.xiangqing.fileArr;
                if (lbImg.length === 0) {
                  this.$message({
                    showClose: true,
                    message: '!!',
                    type: 'error'
                  });
                  return false;
                }
                subObj.goods_groupId = this.goodsForm1.goods_groupId;
                subObj.goods_name = this.goodsForm1.goods_name;
                subObj.goods_label = this.goodsForm1.goods_label;
                subObj.goods_img = imgArr.join(',');
                subObj.list_img = lbImg[0];
                subObj.video = spImg[0] || "";
                subObj.details_img = xqImg[0] || "";
                subObj.warn_num = this.goodsForm6.warn_num;
                subObj.is_dealer = this.goodsForm6.is_dealer ? 1 : 0;
                subObj.dealer_num = this.goodsForm6.dealer_num;
                subObj.buyBack_num = this.goodsForm6.buyBack_num;
                subObj.label = this.goodsForm6.label;
                subObj.salesVolume = this.goodsForm6.salesVolume;
                subObj.num = this.goodsForm6.num;
                subObj.integral = this.goodsForm6.integral;
                subObj.member_price = this.goodsForm6.member_price;
                subObj.trade_price = this.goodsForm6.trade_price;
                subObj.original_price = this.goodsForm6.original_price;
                subObj.dealer_warn_num = this.goodsForm6.dealer_warn_num;
                if (this.yiji == '') {
                  subObj.genre1 = this.goodsForm2.genre1;
                  subObj.genre2 = this.goodsForm2.genre2;
                  subObj.genre5 = this.goodsForm2.genre5 + this.select;
                  subObj.genre7 = this.goodsForm2.genre7;
                } else if (this.yiji == '') {
                  subObj.genre1 = this.goodsForm3.genre1;
                  subObj.genre2 = this.goodsForm3.genre2;
                  subObj.genre3 = this.goodsForm3.genre3;
                  subObj.genre5 = this.goodsForm3.genre5 + this.select;
                  subObj.genre6 = this.goodsForm3.genre6;
                  subObj.genre7 = this.goodsForm3.genre7;
                } else if (this.yiji == '') {
                  subObj.genre8 = this.goodsForm4.genre8;
                }
                subObj.status = 1;
                subObj.id = this.goods.id;
                console.log(subObj)
                this._getData('/api/v1/goods/edit', subObj, data => {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                  });
                  this.$emit('gets');
                  this.$emit('closY');
                  // this.$refs.goodsForm1.resetFields();
                  // if (this.yiji == '') {
                  //   this.$refs.goodsForm2.resetFields();
                  // } else if (this.yiji == '') {
                  //   this.$refs.goodsForm3.resetFields();
                  // } else if (this.yiji == '') {
                  //   this.$refs.goodsForm4.resetFields();
                  // }
                  // this.$refs.goodsForm5.resetFields();
                  // this.$refs.goodsForm6.resetFields();
                  // for (let j = 1; j <= 5; j++) {
                  //   this.$refs['jiuImg' + j].clearUrl();
                  // }
                  // this.$refs['liebiao'].clearUrl();
                  // this.$refs['shipin'].clearUrl();
                  // this.$refs['xiangqing'].clearUrl();
                })
              } else {
                this.$message({
                  showClose: true,
                  message: '!!',
                  type: 'error'
                });
              }
            })
          } else {
            this.$message({
              showClose: true,
              message: '!!',
              type: 'error'
            });
            return false;
          }
        });
      }
    },
    props: ["goods", "dialogVisible"]
  }
</script>

<style scoped>

</style>



