<template>
  <div class="body">
    <!---->
    <div class="tou">
      <div class="font"></div>
    </div>
    <!---->
    <div class="table" style="height:auto;">
      <div class="flex">
        <div class="class"></div>
        <div class="class1">
          <el-select v-model="value" style="width: 450px;margin-left: 5px">
            <el-option
              v-for="item in options"
              :key="item.index"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="flex" style="margin-left:25% ">
        <div style="margin-top: 40px "></div>
        <div style="margin: 30px 0px 0px 50px">
          <el-input v-model="name"  style="width: 450px;" placeholder=""></el-input>
        </div>
      </div>
      <div class="flex" style="margin-left:25% ">
        <div style="margin-top: 40px "></div>
        <div style="margin: 30px 0px 0px 50px">
          <upali class="img_cha" ref="aliComponent" @getUrl="imgUrl1($event, 1)" :fileNumber="1"
                  :imgWidth="120" :imgHeight="120"
                  :defaultImg="photo"></upali>
        </div>
        <div class="add1" style="text-align: center" @click="addClass()">
          
        </div>
      </div>
      
      <!---->
      <div class="biao">
        <el-table
          ref="singleTable"
          :data="classList"
          highlight-current-row
          border
          style="width: 80%;margin-bottom: 30px">
          <el-table-column
            type="index"
            align="center"
            label=""
            width="100">
          </el-table-column>
          <el-table-column
            property="name"
            label=""
            align="center"
            min-width="193">
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            min-width="150">
            <template slot-scope="scope">
              <div>
                <img :src="imggerUrl+scope.row.img" class="imgs">
              </div>
            </template>
          </el-table-column>
          <el-table-column
            property="goods_count"
            label=""
            align="center"
            min-width="130">
          </el-table-column>
          <el-table-column
            min-width="130"
            align="center"
            label="">
            <template slot-scope="scope">
              <div class="flex">
                <div style="color: #0099ce;padding-left: 25px" @click="edit(scope.row)"></div>
                <div style="color: #0099ce;padding-left: 10px" @click="del(scope.row)"></div>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <div>
        <el-dialog
          title=""
          :visible.sync="centerDialogVisible"
          width="30%"
          center>
          <div class="xiu" style="height: 400px">
            <div class="edit"></div>
            <div class="flex">
              <div class="edit1" style="margin-left:20%"></div>
              <div class="edit2" style="margin-left: 0px">
                <el-input v-model="input" placeholder=""></el-input>
              </div>
            </div>
            <div class="flex">
              <div style="margin: 80px 0px 0px 22%"></div>
              <div style="margin: 30px 0px 0px 50px">
                <upali class="img_cha" ref="aliComponent" @getUrl="editimgUrl1($event, 1)" :fileNumber="1"
                       :imgWidth="120" :imgHeight="120"
                       :defaultImg="editphoto"></upali>
              </div>
            </div>
            <div class="flex" style="margin-left: 0%">
              <div class="logBtn1" style="margin-left: 35%" @click="submit()">
                <el-button size="medium" type="primary"></el-button>
              </div>
              <div class="logBtn2">
                <el-button size="medium" @click="esc()"></el-button>
              </div>
            </div>
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
  import upali from '@/components/upload-ali'
  
  export default {
    data() {
      return {
        options: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }],
        value: '',
        name: '',
        classList: [],
        centerDialogVisible: false,
        input: '',
        eid: '',
        photo:'',
        editphoto:'',
        eidtInfo:{}
      }
    },
    watch: {
      value(v, o) {
        this.gclass();
      }
    },
    components: {
      upali
    },
    methods: {
      //
      addClass() {
        if (this.value == '' || this.name == ''|| this.photo=='') {
          this.$message({
            showClose: true,
            message: '',
            type: 'error'
          });
        } else {
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this._getData('/api/v1/goods_group/create', {
                name: this.name,
                group_name: this.value,
                img:this.photo
              },
              data => {
                this.gclass();
                this.$message({
                  type: 'success',
                  message: ''
                });
              })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      },
      //
      gclass() {
        this._getData('/api/v1/goods_group/getSub', {group_name: this.value}, data => {
          this.classList = data;
        })
      },
      //
      edit: function (val) {
        this.centerDialogVisible = true;
        this.eid = val;
      },
      //
      del(val) {
        if (parseInt(val.goods_count) != 0) {
          this.$message({
            showClose: true,
            message: '',
            type: 'error'
          });
        } else {
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this._getData('/api/v1/goods_group/delete', {
                id: val.id
              },
              data => {
                this.$message({
                  type: 'success',
                  message: ''
                });
                this.gclass();
              })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      },
      esc() {
        this.centerDialogVisible = false;
      },
      //  
      submit(val) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/goods_group/edit', {
              id: this.eid.id,
              name: this.input,
              img:this.editphoto
            },
            data => {
              this.$message({
                type: 'success',
                message: ''
              });
              this.gclass();
              this.centerDialogVisible = false;
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
        
      },
      imgUrl1(e) {
        this.photo = e[0];
      },
      editimgUrl1(e) {
        this.editphoto = e[0];
      },
    },
    created() {
      this.gclass();
    }
  }
</script>

<style scoped>
  .font {
    font-size: 18px;
    color: black;
    line-height: 64px;
    text-align: left;
    margin-left: 20px;
  }
  
  /**/
  .table {
    width: 98%;
    border: 1px solid #ddd;
    height: 500px;
  }
  
  .class {
    margin-top: 40px;
    margin-left: 25%;
  }
  
  .class1 {
    margin-top: 30px;
    margin-left: 30px;
  }
  
  .add {
    margin-left: 35%;
    margin-top: 30px;
    width: 185px;
    height: 30px;
  }
  
  .add1 {
    width: 124px;
    height: 40px;
    background-color: rgba(0, 153, 153, 1);
    text-align: center;
    line-height: 40px;
    border-radius: 5px;
    margin-left: 15%;
    margin-top: 50px;
    font-size: 14px;
    color: white;
  }
  
  .biao {
    margin-top: 50px;
    margin-left: 20%;
  }
</style>



