<template>
  <div class="body">
    <!---->
    <div class="flex whiteT">
      <div class="font">ID</div>
      <div class="input">
        <el-input v-model="id" placeholder="ID"></el-input>
      </div>
      
      <div class="font"></div>
      <div class="input">
        <el-input v-model="mobile" placeholder=""></el-input>
      </div>
      
      <div class="font"></div>
      <div class="input">
        <el-input v-model="nickname" placeholder=""></el-input>
      </div>
      
      <!---->
      <div class="btn" style="margin-left: 30%" @click="search()">
        <el-button type="primary" icon="el-icon-search"></el-button>
      </div>
      <div class="btn2" @click="res()">
        <el-button class="buttons3" icon="el-icon-refresh"></el-button>
      </div>
    
    </div>
    
    <!---->
    <div class="head right">
      <div class="head1" @click="exportFunc('dealerTable','')"></div>
      <!--<div class="head1"></div>-->
    </div>
    
    <!---->
    <div id="dealerTable">
      <el-table
        ref="multipleTable"
        :data="dealerList"
        tooltip-effect="dark"
        style="width: 100%"
        border
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          min-width="55">
        </el-table-column>
        <el-table-column
          prop="id"
          label="ID"
          align="center"
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="mobile"
          label=""
          align="center"
          min-width="167">
        </el-table-column>
        <el-table-column
          prop="nickname"
          label=""
          align="center"
          min-width="200">
        </el-table-column>
        <el-table-column
          prop="wallet.balance"
          label=""
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          prop="grade"
          label=""
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="status"
          min-width="150">
          <template slot-scope="scope">
            <div class="flex" v-if="scope.row.status==''">
              <div>&nbsp;&nbsp;</div>
              <div @click="start(scope.row.id)" class="operate">&nbsp;&nbsp;</div>
            </div>
            <div class="flex" v-if="scope.row.status==''">
              <div>&nbsp;&nbsp;</div>
              <div @click="disable(scope.row.id)" class="operate">&nbsp;&nbsp;</div>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          align="center"
          min-width="100"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <!--<div style="color: #0099ce;" @click="info(scope.row.id)"></div>-->
            <div style="color: #0099ce;"></div>
          </template>
        </el-table-column>
      </el-table>
      <div class="pags">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[10, 50, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          :total=totals>
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        input: '',
        multipleSelection: [],
        //  
        dealerList: [],
        //
        page: 1,
        pageSize: 10,
        currentPage4: 1,
        totals: 20,
        id: '',
        mobile: '',
        nickname: ''
      }
    },
    methods: {
      //  
      getDear() {
        this._getData('/api/v1/dealer/index', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          this.dealerList = data.data;
          this.totals = data.total;
        })
      },
      //  
      disable(val) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/dealer/dealerClose', {
              id: val,
            },
            data => {
              this.$message({
                type: 'success',
                message: ''
              });
              this.getDear();
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //  
      start(val) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/dealer/dealerOpen', {
              id: val,
            },
            data => {
              this.$message({
                type: 'success',
                message: ''
              });
              this.getDear();
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //  
      info(val) {
        sessionStorage.setItem('dealerId',val)
        this.$router.push({name: 'dealerInfo'})
      },
      //
      search() {
        var data = {};
        if (this.id != '') {
          data.id = this.id;
        }
        if (this.mobile != '') {
          data.mobile = this.mobile;
        }
        if (this.nickname != '') {
          data.nickname = this.nickname;
        }
        this._getData('/api/v1/dealer/index', {
          page: this.page,
          pageSize: this.pageSize,
          id: data.id,
          mobile: data.mobile,
          nickname: data.nickname
        }, data => {
          this.dealerList = data.data;
          this.totals = data.total;
          this.id = '';
          this.mobile = '';
          this.nickname = '';
        })
      },
      //
      res(){
        this.id = '';
        this.mobile = '';
        this.nickname = '';
        this.getDear();
      },
      //
      handleSizeChange(val) {
        this.pageSize = val;
        if (this.id != '' || this.mobile != '' || this.nickname != '') {
          this.search();
        } else {
          this.getDear();
        }
      },
      //
      handleCurrentChange(val) {
        this.page = val;
        if (this.id != '' || this.mobile != '' || this.nickname != '') {
          this.search();
        } else {
          this.getDear();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }
    },
    created() {
      this.getDear();
    }
  }
</script>

<style scoped>
</style>



