<template>
  <div class="body">
    <router-link to="/shop/shopList">
      <div class="clos"></div>
    </router-link>
    <!---->
    <div class="tou">
      <div class="font"></div>
    </div>
    <div class="table">
      <!---->
      <div class="han" style="margin-top: 20px">
        <div class="Tfont" style="width: 15%;">:&nbsp;&nbsp;&nbsp;&nbsp;{{sid}}</div>
        <div class="Tfont" style="width: 80%;">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.goods_name}}</div>
      </div>
      <!---->
      <div class="han">
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.genre1}}</div>
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.genre3}}</div>
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.genre7}}</div>
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.genre2}}</div>
      </div>
      <!---->
      <div class="han">
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.genre5}}</div>
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.genre6}}</div>
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.genre8}}</div>
      </div>
      <!---->
      <div class="han">
        <div class="Tfont" style="width: 8%;line-height: 100px">:</div>
        <div class="Tfont">
          <img :src="imggerUrl+shopInfo.list_img" class="imgs">
        </div>
      </div>
      <!---->
      <div class="han">
        <div class="Tfont" style="width: 8%;line-height: 100px">:</div>
        <div class="Tfont">
          <img :src="imggerUrl+shopInfo.details_img" class="Tpic">
        </div>
      </div>
      <!---->
      <div class="han">
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.original_price}}</div>
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.member_price}}</div>
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.trade_price}}</div>
        <div class="Tfont" style="width: 20%;">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.genre8}}</div>
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.integral}}</div>
      </div>
      <!---->
      <div class="han">
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.num}}</div>
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.salesVolume}}</div>
      </div>
      <!---->
      <div class="han" style="margin-bottom: 50px;">
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.num}}</div>
        <div class="Tfont">:&nbsp;&nbsp;&nbsp;&nbsp;{{shopInfo.salesVolume}}</div>
      </div>
    </div>
    <!---->
    <div class="tou">
      <div class="font"></div>
    </div>
    <div class="table">
      <div>
        <el-table
          :data="commonts"
          style="width: 100%"
          border>
          <el-table-column
            type="index"
            label=""
            align="center"
            width="60">
          </el-table-column>
          <el-table-column
            prop="user.mobile"
            label=""
            min-width="80"
            align="center">
          </el-table-column>
          <el-table-column
            prop="user.nickname"
            label=""
            align="center"
            min-width="100">
          </el-table-column>
          <el-table-column
            prop="grade"
            label=""
            align="center"
            min-width="100">
          </el-table-column>
          <el-table-column
            prop="grade1"
            label=""
            align="center"
            min-width="80">
          </el-table-column>
          <el-table-column
            prop="grade2"
            label=""
            align="center"
            min-width="80">
          </el-table-column>
          <el-table-column
            prop="content"
            label=""
            align="center"
            min-width="150">
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            min-width="150">
            <template slot-scope="scope">
              <div v-for="(item,index) in scope.row.img.split(',')" :key="index">
                <img :src="imggerUrl+item" class="imgs">
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="create_time"
            label=""
            align="center"
            min-width="100">
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            min-width="80">
            <template slot-scope="scope">
              <div v-if="scope.row.status==''" style="color: deepskyblue" @click="hidden(scope.row.id)"></div>
              <div v-else style="color: deepskyblue" @click="show(scope.row.id)"></div>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            min-width="80">
            <template slot-scope="scope">
              <div style="color: #0099ce;" @click="returns(scope.row)"></div>
            </template>
          </el-table-column>
        </el-table>
        <div class="pags" style="margin:30px 50px 30px 0px">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage4"
            :page-sizes="[20, 50, 100]"
            :page-size="5"
            layout="total, sizes, prev, pager, next, jumper"
            :total=totals>
          </el-pagination>
        </div>
      </div>
    </div>
    <!---->
    <div>
      <el-dialog
        title=""
        :visible.sync="centerDialogVisible"
        width="30%"
        center>
        <div class="xiu">
          <div class="edit"></div>
          <div style="width: 90%;margin:15px 0px 0px 25px;">
            <el-input
              type="textarea"
              :rows="4"
              placeholder=""
              v-model="textarea">
            </el-input>
          </div>
          <div style="margin: 5px 0px 0px 20px;color: #ddd;"></div>
          <div class="flex">
            <div class="logBtn1" style="margin:15px 0px 0px 25%">
              <el-button size="medium" type="primary"></el-button>
            </div>
            <div class="logBtn2" style="margin:15px 0px 0px 10%">
              <el-button size="medium"></el-button>
            </div>
          </div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        input: '',
        centerDialogVisible: false,
        textarea: '',
        //id
        sid: "",
        shopInfo: {},
        commonts: [],
        //
        page: 1,
        pageSize: 20,
        currentPage4: 1,
        totals: 20
      };
    },
    methods: {
      //
      getInfo() {
        this._getData('/api/v1/goods/show', {id: sessionStorage.getItem('ids')}, data => {
          console.log(data)
          this.shopInfo = data;
        })
      },
      //
      getComment() {
        this._getData('/api/v1/comment/goodsComment', {id: sessionStorage.getItem('ids')}, data => {
          console.log(data)
          this.commonts = data;
        })
      },
      //
      handleSizeChange(val) {
        this.pageSize = val;
        this.getComment();
      },
      //
      handleCurrentChange(val) {
        this.page = val;
        this.getComment();
      },
      //
      show(val) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/comment/commentUp', {
              id: val,
            },
            data => {
              this.$message({
                type: 'success',
                message: ''
              });
              this.getComment();
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      hidden(val) {
        console.log(val)
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/comment/commentDown', {
              id: val,
            },
            data => {
              this.$message({
                type: 'success',
                message: ''
              });
              this.getComment();
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      returns() {
        this.centerDialogVisible = true;
      }
    },
    created() {
      this.sid = this.$route.params.id;
      this.getInfo();
      this.getComment();
    }
  }
</script>

<style scoped>
  .font {
    font-size: 18px;
    color: black;
    line-height: 64px;
    text-align: left;
    margin-left: 20px;
  }
  
  .table {
    width: 98%;
    border: 1px solid #ddd;
    height: auto;
  }
  
  /**/
  .han {
    width: 100%;
    display: flex;
  }
  
  .Tfont {
    margin: 15px 0px 0px 50px;
    font-size: 14px;
    width: 15%;
    text-align: left;
  }
  
  .Tpic {
    width: 150px;
    height: 450px;
    border: 1px solid #ddd;
    margin-left: 10px;
  }
</style>



