<template>
  <div class="body">
    <!---->
    <div class="flex" style="margin-top: 60px;">
      <div class="state flex" style="background-color:rgba(0, 153, 153, 1);">
        <div class="state2" style="color: white"></div>
        <div class="state3" style="color: white">({{this.stateList.allCount}})</div>
      </div>
      <div class="state flex">
        <div class="state2"></div>
        <div class="state3">({{this.stateList.goodsUp}})</div>
      </div>
      <div class="state flex">
        <div class="state2"></div>
        <div class="state3">({{this.stateList.goodsDown}})</div>
      </div>
      <div class="state flex">
        <div class="state2"></div>
        <div class="state3">({{this.stateList.dealer}})</div>
      </div>
    </div>
    <div class="search flex">
      <!---->
      <div class="flex">
        <div class="search1">:</div>
        <div class="search2">
          <el-input v-model="bid" size="small" placeholder=""></el-input>
        </div>
      </div>
      <!---->
      <div class="flex">
        <div class="search1">:</div>
        <div class="search2">
          <el-input v-model="goods_name" size="small" placeholder=""></el-input>
        </div>
      </div>
      <!---->
      <div class="flex">
        <div class="search1">:</div>
        <div class="search2">
          <el-select v-model="id" size="small" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </div>
      </div>
      <!---->
      <div class="right flex" style="margin-left: 6%">
        <div class="btn2">
          <el-button class="buttons3" icon="el-icon-refresh" @click="rest()"></el-button>
        </div>
        <div class="btn">
          <el-button type="primary" icon="el-icon-search" @click="search()"></el-button>
        </div>
      </div>
    </div>
    <!---->
    <div>
      <div class="right tou">
        <div class="tou1" @click="batch()"></div>
      </div>
      <el-table
        ref="multipleTable"
        :data="shopList"
        tooltip-effect="dark"
        border
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          label=""
          prop="id"
          align="center"
          min-width="80">
        </el-table-column>
        <el-table-column
          prop="goods_img"
          label=""
          align="center"
          min-width="150">
          <template slot-scope="scope">
            <div>
              <img :src="imggerUrl+scope.row.list_img" class="imgs">
            </div>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="goods_name"
          min-width="120">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          min-width="120">
          <template slot-scope="scope">
            <div>{{scope.row.original_price}}</div>
            <div>{{scope.row.member_price}}</div>
            <div>{{scope.row.trade_price}}</div>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="integral"
          min-width="100">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="order_detail_sum"
          min-width="80">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="num"
          min-width="80">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="salesVolume"
          min-width="80">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="num"
          min-width="80">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          min-width="100"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <div class="sequence">
              <div style="color: #0099ce;padding-left: 10px" @click="stand(scope.row)">
                <div v-if="scope.row.status==''"></div>
                <div v-else></div>
              </div>
              <!--<div style="color: #0099ce;padding-left: 8px" @click="look(scope.row.id)"></div>-->
              <!--<div style="color: #0099ce;padding-left: 10px" @click="edit(scope.row)"></div>-->
              <div style="color: #0099ce;padding-left: 10px" @click="modifyShop(scope.row)"></div>
              <div style="color: #0099ce;padding-left: 8px" @click="del(scope.row.id)"></div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div class="pag">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-size="5"
          layout="total, prev, pager, next"
          :total=totals>
        </el-pagination>
      </div>
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="handleClose">
      <XiuGai @gets="getShop" @closY="closes" :goods="xShop" :dialogVisible="dialogVisible"></XiuGai>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--   -->
  </div>
</template>

<script>
  import XiuGai from '@/components/xiugai'
  export default {
    components: {
      XiuGai
    },
    data() {
      return {
        xShop: {},                    // 
        dialogVisible: false,        // 
        input: '',
        options: [],
        multipleSelection: [],
        //  
        shopList: [],
        page: 1,
        pageSize: 10,
        bid: '',
        goods_name: '',
        id: '',
        currentPage4: 1,
        totals: 10,
        stateList:[],
      };
    },
    methods: {
      // 
      handleClose() {
        console.log('')
        this.dialogVisible = false;
      },
      // 
      modifyShop(row) {
        console.log(row)
        this.xShop = row;
        this.dialogVisible = true;
      },
      //
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //  
      look(val) {
        sessionStorage.setItem('ids', val);
        this.$router.push({name: 'shopInfo'})
      },
      //  
      getShop() {
        this._getData('/api/v1/goods/index', {page: this.page, pageSize: this.pageSize}, data => {
          console.log(data.data)
          this.shopList = data.data;
          this.totals = data.total;
          this.getState();
        })
      },
      //  
      stand(val) {
        console.log(val)
        var ti = '';
        if (val.status == '') {
          ti = ''
        } else {
          ti = ''
        }
        if (ti == '') {
          this.$confirm('' + ti + '?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this._getData('/api/v1/goods/goodsUp', {
                id: val.id,
              },
              data => {
                this.$message({
                  type: 'success',
                  message: ''
                });
                this.getShop();
              })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        } else {
          this.$confirm('' + ti + '?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this._getData('/api/v1/goods/goodsDown', {
                id: val.id,
              },
              data => {
                this.$message({
                  type: 'success',
                  message: ''
                });
                this.getShop();
              })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      },
      //  
      del(val) {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/goods/delete', {id: val}, data => {
            this.$message({
              type: "success",
              message: ''
            })
            this.getShop();
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //  
      gclass() {
        this._getData('/api/v1/goods_group/index', {}, data => {
          this.options = data;
        })
      },
      //  
      search() {
        var data = {};
        if (this.id != '') {
          data.goods_groupId = this.id;
        }
        ;
        if (this.goods_name != '') {
          data.goods_name = this.goods_name;
        }
        ;
        if (this.bid != '') {
          data.id = this.bid;
        }
        data.page = this.page;
        data.pageSize = this.pageSize;
        this._getData('/api/v1/goods/index', {
          page: data.page,
          pageSize: data.pageSize,
          goods_groupId: data.goods_groupId,
          goods_name: data.goods_name,
          id: data.id
        }, data => {
          this.shopList = data.data;
          this.totals = data.total;
        })
      },
      //  
      rest() {
        this.id = "";
        this.goods_groupId = "";
        this.goods_name = "";
        this.getShop();
      },
      //
      closes(){
        this.dialogVisible=false;
      },
      //
      batch() {
        var ids = '';
        var len = this.multipleSelection.length;
        for (var i = 0; i < len; i++) {
          ids = ids + this.multipleSelection[i].id + ',';
        }
        this.$confirm('', '', {
          confirmButtonText: "",
          cancelButtonText: "",
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/goods/batchDelete', {ids: ids}, data => {
            this.$message({
              type: 'success',
              message: ''
            });
            this.getShop();
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      getState(){
        this._getData('/api/v1/goods/goodsUpDownStatistics', {
          }, data => {
            console.log(data)
          this.stateList=data;
          })
      },
      //
      edit(val) {
        this.$router.push({name: 'addShop'})
      },
      handleSizeChange(val) {
        this.pageSize = val;
        if (this.id != '' || this.goods_name != '' || this.bid != '') {
          this.search()
        } else {
          this.getShop();
        }
      },
      //
      handleCurrentChange(val) {
        this.page = val;
        if (this.id != '' || this.goods_name != '' || this.bid != '') {
          this.search()
        } else {
          this.getShop();
        }
      },
    },
    created() {
      this.getShop();
      this.gclass();
      this.getState()
    }
  }
</script>

<style scoped>
  /**/
  .search {
    width: 95%;
    height: 60px;
    margin-left: 10px;
    margin-top: 30px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .search1 {
    font-size: 15px;
    margin-left: 15px;
    line-height: 65px;
    /*color: #ddd;*/
    margin-left: 20px;
  }
  
  .search2 {
    height: 30px;
    width: 150px;
    margin-left: 20px;
    padding-top: 15px;
  }
  
  .btn {
    width: 100px;
    height: 40px;
    margin-left: 10px;
    padding-top: 11px;
  }
  
  .btn2 {
    width: 100px;
    height: 40px;
    margin-left: 10px;
    padding-top: 11px;
  }
  
  /**/
  .tou {
    background-color: #ddd;
    width: 100%;
    height: 60px;
    margin-top: 30px;
  }
  
  .tou1 {
    width: 90px;
    height: 30px;
    border: 1px solid black;
    text-align: center;
    background-color: white;
    margin-top: 15px;
    margin-right: 10px;
    line-height: 30px;
  }
</style>



