<template>
  <div class="body">
    <!---->
    <div class="flex" style="margin-top: 20px;">
      <div class="state flex" style="background-color:rgba(0, 153, 153, 1);">
        <div class="state2" style="color: white"></div>
        <div class="state3" style="color: white">({{this.wineState.allCount}})</div>
      </div>
      <div class="state flex">
        <div class="state2"></div>
        <div class="state3">({{this.wineState.goodsUp}})</div>
      </div>
      <div class="state flex">
        <div class="state2"></div>
        <div class="state3">({{this.wineState.goodsDown}})</div>
      </div>
      <div class="state flex">
        <div class="state2"></div>
        <div class="state3">({{this.wineState.dealer}})</div>
      </div>
    </div>
    <!---->
    <div class="flex whiteT" style="height: 120px">
      <div class="sequence" style="width: 70%;">
        <div class="font" style="margin-left: 5%"></div>
        <div class="input">
          <el-input v-model="input" style="width: 150px" placeholder=""></el-input>
        </div>
        
        <div class="font" style="margin-left: 10%"></div>
        <div class="input">
          <el-input v-model="input" style="width: 150px" placeholder=""></el-input>
        </div>
        
        
        <div class="font" style="margin-left: 10%"></div>
        <div class="input">
          <el-input v-model="input" style="width: 150px" placeholder=""></el-input>
        </div>
        
        <div class="font" style="margin-left: 5%"></div>
        <div class="input">
          <el-date-picker
            v-model="value1"
            style="width: 160px;"
            type="date"
            placeholder="">
          </el-date-picker>
        </div>
        
        <div class="font" style="margin-left: 55px"></div>
        <div class="input">
          <el-date-picker
            v-model="value1"
            style="width: 160px;margin-left: 15px"
            type="date"
            placeholder="">
          </el-date-picker>
        </div>
      </div>
      <!---->
      <div class="btn" style="margin-left: 88px;margin-top: 30px">
        <el-button type="primary" icon="el-icon-search"></el-button>
      </div>
      <div class="btn2" style="margin-left: 5px;margin-top: 30px">
        <el-button class="buttons3" icon="el-icon-refresh"></el-button>
      </div>
    </div>
    <!---->
    <div class="head right">
      <div class="head1"></div>
      <div class="head1"></div>
      <div class="head1"></div>
    </div>
    <!---->
    <div>
      <el-table
        ref="multipleTable"
        :data="dealerList"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        border>
        <el-table-column
          type="selection"
          min-width="55">
        </el-table-column>
        <el-table-column
          label=""
          prop="order_code"
          align="center"
          min-width="130">
        </el-table-column>
        <el-table-column
          prop="create_time"
          label=""
          align="center"
          min-width="130">
        </el-table-column>
        <el-table-column
          prop="goods_info"
          label=""
          align="center"
          min-width="180">
        </el-table-column>
        <el-table-column
          prop="payment_price"
          label=""
          align="center"
          min-width="80">
        </el-table-column>
        <el-table-column
          prop="mode"
          label=""
          align="center"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="user_mobile"
          label=""
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          prop="status"
          label=""
          align="center"
          min-width="130">
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          align="center"
          min-width="137"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <div class="flex">
              <div style="color: #0099ce;padding-left: 0px" @click="look(scope.row.id)"></div>
              <div style="color: #0099ce;padding-left: 4px" @click="del(scope.row.id)"></div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div class="pag">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[20, 50, 100]"
          :page-size="5"
          layout="total, sizes, prev, pager, next, jumper"
          :total=totals>
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        input: '',
        value1: '',
        multipleSelection: [],
        //
        page: 1,
        pageSize: 20,
        currentPage4: 1,
        totals: 10,
        dealerList: [],
        wineState:{}
      }
    },
    methods: {
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //
      getState(){
        this._getData('/api/v1/goods/goodsUpDownStatistics', {
          }, data => {
              console.log(data)
          this.wineState=data;
          })
      },
      //  
      buyList() {
        this._getData('/api/v1/dealer/dealerBuy', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          console.log(data)
          this.dealerList = data.data;
          this.totals = data.total;
        })
      },
      //
      handleSizeChange(val) {
        this.pageSize = val;
        this.buyList();
      },
      //
      handleCurrentChange(val) {
        this.page = val;
        this.buyList();
      },
      //  
      look(val) {
        sessionStorage.setItem('dealID',val)
        this.$router.push({name: 'buyInfo'})
      }
    },
    created() {
      this.buyList();
      this.getState();
    }
  }
</script>

<style scoped>
</style>



