          <div>{{this.orderInfo.payment_time}}</div>
        </div>
        <div class="flex dfont">
          <div></div>
          <div>{{this.orderInfo.order_code}}</div>
        </div>
        <div class="flex dfont" style="width: 100%;">
          <div></div>
          <div>{{this.orderInfo.order_code}}</div>
        </div>
        <div class="flex dfont" style="width: 100%;">
          <div></div>
          <div>{{this.orderInfo.order_code}}</div>
        </div>
      </div>
    </div>
    
    <!---->
