    </div>
    <div class="flex xiu" style="height: auto">
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          prop="date"
          label=""
          align="center"
          min-width="180">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align="center"
          min-width="200">
        </el-table-column>
        <el-table-column
          prop="address"
          align="center"
          min-width="130"
          label="">
        </el-table-column>
        <el-table-column
          prop="date"
          label=""
          align="center"
          min-width="180">
        </el-table-column>
        <el-table-column
          prop="date"
          label=""
          align="center"
          min-width="180">
        </el-table-column>
      </el-table>
    </div>
    
    <!---->
