        </el-table-column>
        <el-table-column
          prop="address"
          align="center"
          min-width="180"
          label="">
        </el-table-column>
        <el-table-column
          prop="date"
          align="center"
          label=""
          min-width="180">
          <template slot="">
            <div style="color: deepskyblue;"></div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    
    <!---->
    <div>
      <el-dialog
        title=""
        :visible.sync="centerDialogVisible"
        width="30%"
        center>
        <div class="xiu">
          <div class="edit"></div>
          <div class="flex futext">
            <el-input
              type="textarea"
              :rows="5"
              placeholder=""
              v-model="textarea">
            </el-input>
          </div>
          <div class="flex">
            <div class="logBtn1" style="margin: 20px 0px 0px 30%;">
              <el-button size="medium" type="primary"></el-button>
            </div>
            <div class="logBtn2" style="margin-top: 20px">
              <el-button size="medium" @click="esc"></el-button>
            </div>
          </div>
        </div>
