  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        textarea: '',
        tableData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }],
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        value: '',
        //  
        centerDialogVisible: false,
        orderInfo: []
      }
    },
    methods: {
      //
      getOrder() {
        this._getData('/api/v1/order/show', {
          id: sessionStorage.getItem('dealID')
        }, data => {
          console.log(data)
          this.orderInfo = data;
        })
      },
      //
      remark() {
        this.centerDialogVisible = true;
      },
      esc() {
        this.centerDialogVisible = false;
      }
    },
    created() {
      this.getOrder();
    }
  }
</script>

<style scoped>
  .dfont {
    margin: 0px 0px 0px 20px;
    width: 30%;
    height: 10px;
    font-size: 14px;
  }
  
  .futext {
    margin: 10px 0px 0px 10px;
    width: 95%;
  }
</style>



