        </el-table-column>
        <el-table-column
          label=""
          prop="status"
          align="center"
          min-width="80">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          min-width="150"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <div>
              <div v-if="scope.row.status==''">
                <div style="color: #0099ce;padding-left: 10px" @click="infos(scope.row.id)"></div>
              </div>
              <div class="sequence" v-if="scope.row.status==''">
                <div style="color: #0099ce;padding-left: 10px" @click="infos(scope.row.id)"></div>
                <div style="color: #0099ce;padding-left: 5px" @click="send(scope.row)"></div>
                <div style="color: #0099ce;padding-left: 10px" @click="picking(scope.row.id)"></div>
                <div style="color: #0099ce;padding-left: 5px" @click="editBelong(scope.row)"></div>
                <div style="color: #0099ce;padding-left: 10px" @click="send(scope.row.id)"></div>
              </div>
              <div class="sequence" v-if="scope.row.status==''">
                <div style="color: #0099ce;padding-left: 10px" @click="infos(scope.row.id)"></div>
                <div style="color: #0099ce;padding-left: 10px" @click="send(scope.row.id)"></div>
                <div style="color: #0099ce;padding-left: 10px" @click="send(scope.row.id)"></div>
              </div>
              <div class="sequence" v-if="scope.row.status==''">
                <div style="color: #0099ce;padding-left: 10px" @click="infos(scope.row.id)"></div>
                <div style="color: #0099ce;padding-left: 10px" @click="del(scope.row.id)"></div>
              </div>
              
              <div class="sequence" v-if="scope.row.status==''">
                <div style="color: #0099ce;padding-left: 10px" @click="infos(scope.row.id)"></div>
                <div style="color: #0099ce;padding-left: 10px" @click="del(scope.row.id)"></div>
              </div>
              
              <div class="sequence" v-if="scope.row.status==''">
                <div style="color: #0099ce;padding-left: 10px" @click="infos(scope.row.id)"></div>
                <div style="color: #0099ce;padding-left: 10px" @click="del(scope.row.id)"></div>
              </div>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pag">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[20, 50, 100]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total=totals>
      </el-pagination>
    </div>
    <!---->
    <div>
      <el-dialog
        title=""
        :visible.sync="centerDialogVisible"
        width="34%"
        center>
        <div class="xiu" style="height: auto;">
          <div class="edit"></div>
          <div class="address">{{this.orderInfo[0].address}}</div>
          <div class="flex addInput">
            <div class="addFont"></div>
            <div>
              <el-select v-model="value" size="medium" placeholder="" style="width: 50%">
                <el-option
                  v-for="item in trans_company"
                  :key="item.id"
                  :label="item.name"
                  :value="item.shortName">
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="flex addInput">
            <div class="addFont"></div>
            <div class="">
              <el-input v-model="input" size="medium" placeholder=""></el-input>
            </div>
          </div>
          <div class="addFont" style="margin-left: 30px"></div>
          <div style="margin: 10px 0px 0px 30px;">
            <el-table
              :data="orderInfo[0].goods_info"
              border
              style="width: 80%">
              <el-table-column
                prop="goods_name"
                label=""
                align="center"
                min-width="180">
              </el-table-column>
              <el-table-column
                label=""
                align="center"
                min-width="250">
                <template slot-sope="scope">
                  <el-select v-model="value" size="medium" placeholder="" style="width: 93%">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div class="flex">
            <div class="logBtn1" style="margin: 30px 0px 0px 33%">
              <el-button size="medium" type="primary"></el-button>
            </div>
            <div class="logBtn2" style="margin: 30px 0px 0px 20px">
              <el-button size="medium"></el-button>
            </div>
          </div>
        </div>
      </el-dialog>
    </div>
    <!---->
    <div>
      <el-dialog
        title=""
        :visible.sync="sendPicking"
        width="30%"
        center>
        <div class="xiu" style="height: 300px">
          <div class="edit"></div>
          <div class="flex addInput">
            <div class="Paddfont"></div>
            <div>
              <el-select v-model="value" size="medium" placeholder="" style="width: 90%">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
