    </div>
    <!---->
    <div>
      <el-dialog
        title=""
        :visible.sync="belong"
        width="30%"
        center>
        <div class="xiu" style="height: 250px">
          <div class="edit"></div>
          <div class=" addInput">
            <div class="Paddfont" style="width: 60%;margin: 20px 0px 20px 10px;text-align: left"></div>
            <div>
              <el-select v-model="consignerType" size="medium" placeholder="" style="width: 70%;margin-left: 50px">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
