          </div>
          <div class="flex">
            <div class="logBtn1" style="margin: 30px 0px 0px 80px" @click="submit()">
              <el-button size="medium" class="buttons" type="primary"></el-button>
            </div>
            <div class="logBtn2" style="margin: 30px 0px 0px 20px" @click="esc()">
              <el-button size="medium" class="buttons"></el-button>
            </div>
          </div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import Ostate from '@/components/orderStat'
  export default {
    data() {
      return {
        input: '',
        value1: '',
        tableData3: [{
          date: '2016-05-03',
          name: '',
          address: ''
        }],
        options: [{
          value: '1',
          label: ''
        }, {
          value: '3',
          label: ''
        }],
        consignerType: '',
        multipleSelection: [],
        //
        centerDialogVisible: false,
        //  
        sendPicking: false,
        //  
        belong: false,
        //  
        orderState: false,
        //
        page: 1,
        pageSize: 10,
        currentPage4: 1,
        totals: 20,
        orderList: [],
        orderStatus: [],
        //  
        editinfo: {},
        value: '',
        orderInfo: [{address: ''}],
        ids:'',
        //
        inventory:[],
        trans_company: [],
      }
    },
    components:{
      Ostate
    },
    methods: {
      //
      getOrder() {
        this._getData('/api/v1/order/index', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          console.log(data)
          this.orderList = data.data;
          this.totals = data.total;
        })
      },
      //
      Status() {
        this._getData('/api/v1/order/status', {}, data => {
          console.log(data)
          this.orderStatus = data.data;
        })
      },
      //
      infos(val) {
        sessionStorage.setItem('orderId', val);
        this.$router.push({name: 'orderInfo'})
      },
      //
      submit() {
        this._getData('/api/v1/order/editBelong', {
          id: this.editinfo.id,
          consignerType: this.consignerType,
          userCode: this.editinfo.user_code
        }, data => {
          this.getOrder();
          this.belong = false;
          this.$message({
            type: 'success',
            message: ''
          });
        })
      },
      esc() {
        this.belong = false;
      },
      //
      del(val) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/order/delete', {
              id: val,
            },
            data => {
              this.$message({
                type: 'success',
                message: ''
              });
              this.getOrder();
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      batch(){
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/order/batchDelete', {
              ids: this.ids,
            },
            data => {
              this.$message({
                type: 'success',
                message: ''
              });
              this.getOrder();
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      handleSelectionChange(val) {
        var ids='';
        for(let i=0;i<val.length;i++){
          ids+=val[i].id + ',';
        }
        this.ids=ids;
      },
      //  
      send(val) {
        console.log(val)
        //
        this._getData('/api/v1/order/index', {
          order_code: val.order_code,
        }, data => {
          console.log(data)
          this.orderInfo = data.data;
        })
        //
        this._getData('/api/v1/order/getWarehouse', {
          id: val.id,
        }, data => {
          console.log(data)
          this.inventory = data;
        })
        //
        this._getData('/api/v1/trans_company/index', {
        }, data => {
          console.log(data)
          this.trans_company = data;
        })
        this.centerDialogVisible = true;
      },
      //  
      picking() {
        this.sendPicking = true;
      },
      //  
      editBelong(val) {
        console.log(val)
        this.editinfo = val;
        this.belong = true;
      },
      //
      handleSizeChange(val) {
        this.pageSize = val;
        this.getOrder();
      },
      //
      handleCurrentChange(val) {
        this.page = val;
        this.getOrder();
      },
    },
    created() {
      this.getOrder();
      this.Status();
    }
  }
</script>

<style scoped>
  .address {
    margin: 20px 0px 0px 25px;
  }

</style>



