<template>
  <div class="body">
    <!---->
    <div class="flex" style="margin-top: 30px;">
      <div :class="[ 'state','flex', dealer==true?'bj':'']" @click="dealerInfo()">
        <div class="state2"></div>
      </div>
      <div :class="[ 'state','flex', members==true?'bj':'']" @click="member()" style="margin-left: 0px">
        <div class="state2"></div>
      </div>
      <router-link to="/dealer/buyRecord">
        <div :class="[ 'state','flex']" style="margin-left: 0px">
          <div class="state2"></div>
        </div>
      </router-link>
    </div>
    <!---->
    <div class="flex whiteT">
      <div class="font"></div>
      <div class="input">
        <el-input v-model="user_mobile" placeholder=""></el-input>
      </div>
      
      <div class="font"></div>
      <div class="input">
        <el-input v-model="goods_name" placeholder=""></el-input>
      </div>
      
      <div class="font"></div>
      <div class="input">
        <el-date-picker
          v-model="value1"
          style="width: 150px;"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </div>
      
      <div class="font" style="margin-left: 4%"></div>
      <div class="input" style="margin-left: 2%">
        <el-date-picker
          v-model="value2"
          value-format="yyyy-MM-dd"
          style="width: 150px;"
          type="date"
          placeholder="">
        </el-date-picker>
      </div>
      
      <!---->
      <div class="btn" style="margin-left:10%" @click="search()">
        <el-button type="primary" icon="el-icon-search"></el-button>
      </div>
      <div class="btn2" style="margin-left: 5px" @click="res()">
        <el-button class="buttons3" icon="el-icon-refresh"></el-button>
      </div>
    
    </div>
    <!--   -->
    <div v-if="dealer==true">
      <div class="head join">
        <div class="head1" style="margin-left: 20px" @click="manage()"></div>
      </div>
      <!---->
      <div>
        <el-table
          ref="multipleTable"
          :data="Pterrace"
          tooltip-effect="dark"
          style="width: 100%"
          border>
          <el-table-column
            type="index"
            label=""
            align="center"
            width="55">
          </el-table-column>
          <el-table-column
            label="ID"
            prop="user_id"
            align="center"
            min-width="120">
          </el-table-column>
          <el-table-column
            prop="user_mobile"
            label=""
            align="center"
            min-width="120">
          </el-table-column>
          <el-table-column
            label=""
            min-width="200"
            align="center"
            show-overflow-tooltip>
            <template slot-scope="scope">
              <div v-for="(item,index) in scope.row.order_detail" :key="index">
                <div style="border: 1px solid #ddd">
                  {{item.goods_name}}
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            min-width="97">
            <template slot-scope="scope">
              <div v-for="(item,index) in scope.row.order_detail" :key="index">
                <div style="border: 1px solid #ddd">
                  {{item.price}}
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            min-width="130">
            <template slot-scope="scope">
              <div v-for="(item,index) in scope.row.order_detail" :key="index">
                <div style="border: 1px solid #ddd">
                  {{item.integral}}
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="create_time"
            label=""
            align="center"
            min-width="130">
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            min-width="80">
            <template slot-scope="scope">
              <div v-for="(item,index) in scope.row.order_detail" :key="index">
                <div style="border: 1px solid #ddd">
                  {{item.num}}
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="day"
            label=""
            align="center"
            min-width="80">
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            min-width="80"
            show-overflow-tooltip>
            <template slot-scope="scope">
              <div style="color: #0099ce;" @click="force(scope.row)"></div>
            </template>
          </el-table-column>
        </el-table>
        <div class="pag">
          <el-pagination
            @size-change="PhandleSizeChange"
            @current-change="PhandleCurrentChange"
            :current-page="currentPage4"
            :page-sizes="[20, 50, 100]"
            :page-size="5"
            layout="total, sizes, prev, pager, next, jumper"
            :total=totals>
          </el-pagination>
        </div>
      </div>
      <!---->
      <div>
        <el-dialog
          title=""
          :visible.sync="centerDialogVisible"
          width="35%"
          center>
          <div class="xiu">
            <div class="edit"></div>
            <div class="flex">
              <div class="edit1" style="margin-left: 20%"></div>
              <div class="edit2">
                <el-input v-model="Hday" placeholder=""></el-input>
              </div>
            </div>
            <div class="flex">
              <div class="logBtn1" style="margin-left: 35%;">
                <el-button size="medium" type="primary" @click="submit()"></el-button>
              </div>
              <div class="logBtn2">
                <el-button size="medium"></el-button>
              </div>
            </div>
          </div>
        </el-dialog>
      </div>
    </div>
    <!---->
    <div v-else>
      <div class="head join">
      </div>
      <el-table
        ref="multipleTable"
        :data="Jterrace"
        tooltip-effect="dark"
        style="width: 100%"
        border>
        <el-table-column
          type="index"
          label=""
          align="center"
          width="55">
        </el-table-column>
        <el-table-column
          label=""
          prop="create_time"
          align="center"
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="user_id"
          label="ID"
          align="center"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="user_mobile"
          label=""
          min-width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="goods_name"
          label=""
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          prop="price"
          label=""
          align="center"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="goods_integral"
          label=""
          align="center"
          min-width="130">
        </el-table-column>
        <el-table-column
          prop="order_time"
          label=""
          align="center"
          min-width="110">
        </el-table-column>
        <el-table-column
          prop="num"
          label=""
          align="center"
          min-width="80">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          min-width="100"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <div class="flex">
              <div style="color: #0099ce;" @click="del(scope.row.id)">&nbsp;&nbsp;</div>
              <div style="color: #0099ce;" @click="del(scope.row.id)">&nbsp;&nbsp;</div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div class="pag">
        <el-pagination
          @size-change="JPhandleSizeChange"
          @current-change="JPhandleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[20, 50, 100]"
          :page-size="5"
          layout="total, sizes, prev, pager, next, jumper"
          :total=Jtotal>
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        input: '',
        value1: '',
        tableData3: [{
          date: '2016-05-03',
          name: '',
          address: ''
        }],
        multipleSelection: [],
        centerDialogVisible: false,
        //  
        dealer: true,
        members: false,
        //
        page: 1,
        pageSize: 10,
        currentPage4: 1,
        totals: 10,
        Jtotal: 10,
        //
        Pterrace: [],
        Jterrace: [],
        Hday: '',
        user_mobile: '',
        goods_name: '',
        value1: "",
        value2: ''
      }
    },
    methods: {
      manage() {
        this.centerDialogVisible = true;
      },
      //
      getdealer() {
        this._getData('/api/v1/dealer/buyback', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          console.log('')
          console.log(data)
          this.Pterrace = data.data;
          this.totals = data.total;
        })
      },
      //
      PhandleSizeChange(val) {
        this.pageSize = val;
        this.getService();
      },
      //
      PhandleCurrentChange(val) {
        this.page = val;
        this.getService();
      },
      //
      JPhandleSizeChange(val) {
        this.pageSize = val;
        this.getJin();
      },
      //
      JPhandleCurrentChange(val) {
        this.page = val;
        this.getJin();
      },
      //
      getJin() {
        this._getData('/api/v1/buyback_record/dealerApply', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          console.log('')
          console.log(data)
          this.Jterrace = data.data;
          this.Jtotal = data.total;
        })
      },
      //
      submit() {
        this._getData('/api/v1/buyback_record/buybackTime', {
            time: this.Hday,
          },
          data => {
            this.$message({
              type: 'success',
              message: ''
            });
            this.getdealer();
            this.centerDialogVisible = false;
          })
      },
      //
      force(val) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/buyback_record/coerce', {
              user_code: val.user_code,
              order_code: val.order_code,
              goodsId: val.goodsId,
              num: val.num,
              price: val.price,
            },
            data => {
              this.$message({
                type: 'success',
                message: ''
              });
              this.getdealer();
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      search() {
        console.log(value1)
        console.log(value2)
        var data = this.goods_name;
        if (this.goods_name != '') {
          data.goods_name = this.goods_name;
        }
        if (this.user_mobile != '') {
          data.user_mobile = this.user_mobile;
        }
        if (this.value1 != '') {
          data.start_time = this.value1;
        }
        if (this.value2 != '') {
          data.end_time = this.end_time;
        }
        //
        this._getData('/api/v1/buyback_record/dealerApply', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          console.log(data)
          this.Jterrace = data.data;
          this.totals = data.total;
        })
      },
      //
      dealerInfo() {
        this.getdealer();
        this.dealer = true;
        this.members = false;
      },
      //  
      member() {
        this.dealer = false;
        this.members = true;
        this.getJin();
      }
    },
    created() {
      this.getdealer();
    }
  }
</script>

<style scoped>
</style>



