<template>
  <div class="body" style="width: 99.8%;margin-left: 0px">
    <div class="flex whiteT">
      <div class="font"></div>
      <div class="Xinput">
        <el-input v-model="input" size="small" placeholder=""></el-input>
      </div>
      
      <div class="font"></div>
      <div class="Xinput">
        <el-input v-model="input" size="small" placeholder=""></el-input>
      </div>
      
      <div class="font"></div>
      <div class="input">
        <el-date-picker
          v-model="value6"
          size="small"
          style="width: 250px;margin-top: 5px"
          type="datetimerange"
          start-placeholder=""
          end-placeholder=""
          :default-time="['12:00:00']">
        </el-date-picker>
      </div>
      
      <!---->
      <div class="btn" style="margin-left: 20%">
        <el-button type="primary"  icon="el-icon-search"></el-button>
      </div>
      <div class="btn2">
        <el-button class="buttons3" icon="el-icon-refresh"></el-button>
      </div>
    </div>
    <div style="margin-top: 20px">
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          label=""
          type="index"
          align="center"
          width="80">
        </el-table-column>
        <el-table-column
          prop="data"
          label=""
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          prop="name"
          align="center"
          label=""
          min-width="180">
        </el-table-column>
        <el-table-column
          prop="address"
          min-width="250"
          align="center"
          label="">
        </el-table-column>
        <el-table-column
          prop="address"
          min-width="180"
          align="center"
          label="">
        </el-table-column>
        <el-table-column
          prop="address"
          min-width="150"
          align="center"
          label="">
          <template slot-scope="scope">
            <div>
              <el-input v-model="input"></el-input>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="address"
          min-width="100"
          align="center"
          label="">
          <template slot-scope="scope">
            <div style="color:deepskyblue"></div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pag">
      <pages></pages>
    </div>
  </div>
</template>

<script>
  import pages from '@/components/page'
  export default {
    name: "",
    data() {
      return {
        tableData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }],
        input: '',
        value6: ''
      }
    },
    components:{
      pages,
    }
  }
</script>

<style scoped>
</style>



