<template>
  <div class="body">
    <div class="flex xiu" style="height: 120px;margin-top: 40px">
      <div style="margin-left: 60px">
        <img :src="imggerUrl+this.dealerList.photo"  class="imgs">
      </div>
      <div class="sequence" style="width: 70%;margin: 30px 0px 0px 0px">
        <div class="flex pian" style="margin-left: 30px">
          <div>ID</div>
          <div>{{this.dealerList.id}}</div>
        </div>
        <div class="flex pian">
          <div></div>
          <div>{{this.dealerList.mobile}}</div>
        </div>
        <div class="flex pian">
          <div></div>
          <div>{{this.dealerList.create_time}}</div>
        </div>
        <div class="flex pian">
          <div></div>
          <div>{{this.dealerList.alliance}}</div>
        </div>
        <div class="flex pian" style="margin-left: 32px;">
          <div></div>
          <div>{{this.dealerList.nickname}}</div>
        </div>
        <div class="flex pian">
          <div></div>
          <div>{{this.dealerList.birthday}}</div>
        </div>
        <div class="flex pian">
          <div></div>
          <div>{{this.dealerList.sex}}</div>
        </div>
        <div class="flex pian">
          <div></div>
          <div>{{this.dealerList.mobile}}</div>
        </div>
      </div>
      <router-link to="/dealer/dealerList">
        <div class="clos"></div>
      </router-link>
    </div>
    <!---->
    <div class="flex" style="margin-top: 30px;">
      <div :class="[ 'state','flex', dealer==true?'bj':'']" @click="dealerInfo()">
        <div class="state2"></div>
      </div>
      <div :class="[ 'state','flex', members==true?'bj':'']" @click="member()" style="margin-left: 0px">
        <div class="state2"></div>
      </div>
    </div>
    <!---->
    <div v-if="dealer==true">
      <Dinfo ref="headerChild"></Dinfo>
    </div>
    <div v-else>
      <MemberInfo ></MemberInfo>
    </div>
  </div>
</template>

<script>
  import Dinfo from '@/components/delaer/Dinfo'
  import MemberInfo from '@/components/delaer/MemberInfo'
  
  export default {
    components: {
      Dinfo,
      MemberInfo
    },
    data() {
      return {
        tableData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }],
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        value: '',
        //  
        dealer: true,
        members: false,
        dealerList:{}
      }
    },
    methods: {
      //
      dealerInfo() {
        this.dealer = true;
        this.members = false;
      },
      //  
      getDealer() {
        this._getData('/api/v1/user/show', {
          id: sessionStorage.getItem('dealerId'),
        }, data => {
          console.log(data)
          this.dealerList=data;
        })
      },
      //  
      member() {
        this.dealer = false;
        this.members = true;
      },
    },
    created(){
      this.getDealer();
    }
  }
</script>

<style scoped>
  
  .font {
    text-align: left;
    font-size: 18px;
    font-weight: 600;
    color: #333;
    line-height: 50px;
  }
  
  /**/
  .imgs {
    border: 1px solid #ddd;
    width: 80px;
    height: 80px;
    margin-top: 20px;
  }
  
  .pian {
    width: 23%;
    font-size: 15px;
    /*margin-left: 10%;*/
  }
  
  .bj {
    background-color: rgba(134, 134, 134, 1);
    color: white
  }
</style>



