<template>
  <div class="body">
    <div class="flex whiteT">
      <div class="font">ID</div>
      <div class="input">
        <el-input v-model="id" style="width: 150px" placeholder="ID"></el-input>
      </div>
      
      <div class="font" style="margin-left: 50px"></div>
      <div class="input">
        <el-input v-model="name" style="width: 250px" placeholder=""></el-input>
      </div>
      
      <!---->
      <div class="btn" style="margin-left:30%" @click="search()">
        <el-button type="primary"  icon="el-icon-search"></el-button>
      </div>
      <div class="btn2" style="margin-left: 5px" @click="res()">
        <el-button class="buttons3" icon="el-icon-refresh"></el-button>
      </div>
    
    </div>
    <!---->
    <div class="head join">
      <div class="head1" style="margin-left: 20px" @click="manage()">
        <div class="flex">
          <div><img src="@/img/add.png" class="addimg" alt=""></div>
          <div>&nbsp;&nbsp;</div>
        </div>
      </div>
      <div class="head1" style="margin-right: 50px"></div>
    </div>
    <!---->
    <div>
      <el-table
        ref="multipleTable"
        :data="wareList"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        border>
        <el-table-column
          type="selection"
          min-width="55">
        </el-table-column>
        <el-table-column
          prop="id"
          label=""
          align="center"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          prop="city"
          align="center"
          min-width="250"
          label=""
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="linkman"
          label=""
          align="center"
          min-width="117">
        </el-table-column>
        <el-table-column
          prop="mobile"
          label=""
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          align="center"
          min-width="250"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <div class="flex">
              <div style="color: #0099ce;padding-left: 8px" @click="inven(scope.row.id)"></div>
              <div style="color: #0099ce;padding-left: 8px" @click="bank(scope.row.id)"></div>
              <div style="color: #0099ce;padding-left: 4px" @click="bank(scope.row.id)"></div>
              <div style="color: #0099ce;padding-left: 4px" @click="edit(scope.row.id)"></div>
              <div style="color: #0099ce;padding-left: 4px" @click="del(scope.row.id)"></div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div class="pag">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[20, 50, 100]"
          :page-size="5"
          layout="total, sizes, prev, pager, next, jumper"
          :total=totals>
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        input: '',
        value1: '',
        tableData3: [{
          date: '2016-05-03',
          name: '',
          address: ''
        }],
        multipleSelection: [],
        centerDialogVisible: false,
        //
        page: 1,
        pageSize: 20,
        currentPage4: 1,
        totals: 20,
        wareList: [],
        id:'',
        name:''
      }
    },
    methods: {
      //
      getWare() {
        this._getData('/api/v1/warehouse/index', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          console.log(data)
          this.wareList = data.data;
          this.totals = data.total;
        })
      },
      //
      search(){
        var data={};
        if(this.id!=''){
          data.id=this.id;
        }
        if(this.name!=''){
          data.name=this.name;
        }
        this._getData('/api/v1/warehouse/index', {
          page: this.page,
          pageSize: this.pageSize,
          id:data.id,
          name:data.name
        }, data => {
          console.log(data)
          this.wareList = data.data;
          this.totals = data.total;
        })
      },
      //
      res(){
        this.id='';
        this.name='';
        this.getWare();
      },
      //
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //
      handleSizeChange(val) {
        this.pageSize = val;
        this.getWare();
      },
      //
      handleCurrentChange(val) {
        this.page = val;
        this.getWare();
      },
      manage() {
        this.$router.push({name: 'editWare'})
      },
      //  
      inven() {
        this.$router.push({name: 'inventoryInfo'})
      },
      //  
      edit() {
        this.$router.push({name: 'editWare'})
      },
      //  
      bank() {
        this.$router.push({name: 'ruBank'})
      }
    },
    created() {
      this.getWare();
    }
  }
</script>

<style scoped>
</style>



