<template>
  <div class="body">
    <div class="title">"&nbsp;&nbsp;&nbsp;&nbsp;{{this.joinInfo.name}}&nbsp;&nbsp;&nbsp;&nbsp;"</div>
    <!---->
    <div class="flex sou">
      <div class="fonts">ID</div>
      <div>
        <el-input v-model="gid" class="pian" size="medium" style="width: 150px" placeholder=""></el-input>
      </div>
      <div class="fonts" style="margin-left: 20px"></div>
      <div>
        <el-input v-model="goods_name" class="pian" size="medium" style="width: 180px" placeholder=""></el-input>
      </div>
      <div style="margin: 5px 0px 0px 50px" @click="search()">
        <el-button type="primary" size="small" icon="el-icon-search"></el-button>
      </div>
      <div style="margin: 5px 0px 0px 20px" @click="res()">
        <el-button size="small" class="buttons2" icon="el-icon-refresh"></el-button>
      </div>
    </div>
    <!---->
    <div class="tables">
      <el-table
        ref="multipleTable"
        :data="shopList"
        tooltip-effect="dark"
        style="width:80%"
        border>
        <el-table-column
          type="index"
          label=""
          align="center"
          width="80">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="id"
          min-width="125">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align="center"
          min-width="150">
          <template slot-scope="scope">
            <div>
              <img :src="imggerUrl+scope.row.list_img" class="imgs">
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="goods_name"
          label=""
          align="center"
          min-width="200">
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          align="center"
          min-width="120"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <div style="width: 120px;" @click="query(scope.row.id)">
              <el-button size="small" style="background-color: rgba(0, 153, 153, 1);color: white"></el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pag" style="margin:50px 15% 0px 20px">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[20, 50, 100]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total=totals>
      </el-pagination>
    </div>
    
    <!---->
    <div class="tables">
      <el-table
        ref="multipleTable"
        :data="inventory"
        tooltip-effect="dark"
        style="width:80%"
        @selection-change="handleSelectionChange"
        border>
        <el-table-column
          type="selection"
          align="center"
          width="80">
        </el-table-column>
        <el-table-column
          label=""
          align="warehouseId"
          prop="name"
          min-width="125">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          prop="city"
          label=""
          align="center"
          min-width="200">
        </el-table-column>
        <el-table-column
          prop="num"
          label=""
          align="center"
          min-width="120"
          show-overflow-tooltip>
        </el-table-column>
      </el-table>
    </div>
    <!---->
    <div class="flex inputs">
      <div style="line-height: 35px"></div>
      <div>
        <el-input v-model="num" size="medium" placeholder=""></el-input>
      </div>
    </div>
    <!---->
    <div class="flex">
      <div style="margin: 30px 0px 0px 33%" @click="submit()">
        <el-button class="buttons buttons2"></el-button>
      </div>
      <div style="margin: 30px 0px 0px 10%">
        <router-link to="/join/join">
          <el-button class="buttons"></el-button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        num: '',
        joinInfo: {},
        //
        page: 1,
        pageSize: 10,
        currentPage4: 1,
        totals: 20,
        shopList: [],
        inventory:[],
        submitData:'',
        multipleSelection: [],
        goods_name:'',
        gid:''
      }
    },
    methods: {
      //
      getShop() {
        this._getData('/api/v1/goods/index', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          console.log(data)
          this.shopList = data.data;
          this.totals = data.total;
        })
      },
      //
      query(val) {
        this._getData('/api/v1/alliance/getWarehouseByGoodsId', {
          id: val
        }, data => {
          console.log(data)
          this.inventory=data;
          this.submitData=val;
        })
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //
      submit(){
        console.log(this.multipleSelection)
        if(this.multipleSelection.length>1){
          this.$message({
            type: 'info',
            message: ''
          });
        }else{
          this._getData('/api/v1/alliance/orderGoods', {
            allianceWarehouseId:this.joinInfo.id,
            lack:this.joinInfo.lack,
            warehouseId:this.multipleSelection[0].warehouseId,
            goodsId:this.multipleSelection[0].goodsId,
            num:this.num
          }, data => {
            this.getShop();
            this.$message({
              type: 'success',
              message: ''
            });
          })
        }
      },
      //
      search(){
        var data={};
        if(this.gid!=''){
          data.gid=this.gid;
        }
        if(this.goods_name!=''){
          data.goods_name=this.goods_name;
        }
        this._getData('/api/v1/goods/index', {
          page: this.page,
          pageSize: this.pageSize,
          id:data.gid,
          goods_name:data.goods_name
        }, data => {
          console.log(data)
          this.shopList = data.data;
          this.totals = data.total;
        })
      },
      //
      res(){
        this.gid='';
        this.goods_name='';
        this.getShop();
      },
      //
      handleSizeChange(val) {
        this.pageSize = val;
        this.getShop();
      },
      //
      handleCurrentChange(val) {
        this.page = val;
        this.getShop();
      },
    },
    created() {
      this.getShop();
      this.joinInfo=JSON.parse( sessionStorage.getItem("joinInfo"))
      console.log(this.joinInfo)
    }
  }
</script>

<style scoped>
  .title {
    font-size: 16px;
    text-align: left;
    margin: 40px 0px 0px 16%;
  }
  
  .sou {
    margin: 20px 0px 0px 16%;
  }
  
  .fonts {
    line-height: 40px;
  }
  
  .pian {
    margin-left: 20px;
  }
  
  .tables {
    margin: 30px 0px 0px 18%;
  }
  
  .inputs {
    margin: 50px 0px 0px 38%;
  }
  
  .buttons {
    width: 140px;
  }
</style>



