<template>
  <div class="body">
    <!---->
    <div class="flex whiteT">
      <div class="font">ID</div>
      <div class="input">
        <el-input v-model="id" placeholder="ID"></el-input>
      </div>
      
      <div class="font"></div>
      <div class="input">
        <el-input v-model="name" placeholder=""></el-input>
      </div>
      
      <div class="font"></div>
      <div class="input">
        <el-input v-model="district" placeholder=""></el-input>
      </div>
      
      <!---->
      <div class="btn" @click="search()">
        <el-button type="primary" icon="el-icon-search"></el-button>
      </div>
      <div class="btn2" @click="res()">
        <el-button type="primary" icon="el-icon-refresh"></el-button>
      </div>
    
    </div>
    <!---->
    <div class="head join">
      <router-link to="/join/addJoin">
        <div class="head1 flex" style="margin-left: 20px">
          <div><img src="@/img/add.png" class="addimg"></img></div>
          <div style="font-size: 11px">&nbsp;</div>
        </div>
      </router-link>
      <div class="right">
        <div class="head1"></div>
        <div class="head1" @click="batch()"></div>
      </div>
    </div>
    <!---->
    <div>
      <el-table
        ref="multipleTable"
        :data="joinList"
        tooltip-effect="dark"
        style="width: 100%"
        border
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          min-width="55">
        </el-table-column>
        <el-table-column
          prop="id"
          label="ID"
          align="center"
          min-width="90">
        </el-table-column>
        <el-table-column
          prop="district"
          label=""
          align="center"
          min-width="90">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          align="center"
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="mobile"
          label=""
          align="center"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="fee"
          label=""
          align="center"
          min-width="90">
        </el-table-column>
        <el-table-column
          prop="divide"
          label=""
          align="center"
          min-width="90">
        </el-table-column>
        <el-table-column
          prop="time"
          label=""
          align="center"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="lack"
          label=""
          align="center"
          min-width="95">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          min-width="111"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <div class="sequence" style="width: 120px">
              <div style="color: #0099ce;padding-left: 15px" @click="stock(scope.row.id)"></div>
              <div style="color: #0099ce;padding-left: 10px" @click="edit(scope.row)"></div>
              <div style="color: #0099ce;padding-left: 16.5px" @click="order(scope.row)"></div>
              <div style="color: #0099ce;padding-left: 10px" @click="del(scope.row.id)"></div>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        input: '',
        ids:'',
        //
        joinList:[],
        page: 1,
        pageSize: 20,
        currentPage4: 1,
        totals: 20,
        id:'',
        name:'',
        district:''
      }
    },
    methods: {
      //
      getJoin() {
        this._getData('/api/v1/alliance/index', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          this.joinList=data.data;
          this.totals=data.total;
          console.log(data)
        })
      },
      //
      stock(val) {
        this.$router.push({name: 'joinStock'})
        sessionStorage.setItem('joinId', val);
      },
      //
      order(val) {
        sessionStorage.setItem('joinInfo', JSON.stringify(val));
        this.$router.push({name: 'joinOrder'});
        
      },
      //
      edit() {
        this.$router.push({name: 'editJoin'});
      },
      //
      search(){
        var data={};
        if(this.id!=''){
          data.id=this.id;
        }
        if(this.name!=''){
          data.name=this.name;
        }
        if(this.district!=''){
          data.district=this.district;
        }
        this._getData('/api/v1/alliance/index', {
          page: this.page,
          pageSize: this.pageSize,
          id:data.id,
          name:data.name,
          district:data.district
        }, data => {
          this.joinList=data.data;
          this.totals=data.total;
        })
      },
      //
      res(){
        this.id='';
        this.name='';
        this.district='';
        this.getJoin();
      },
      //
      del(val){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/alliance/delete', {
              id: val,
            },
            data => {
              this.$message({
                type: 'success',
                message: ''
              });
              this.getJoin();
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      batch(){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/alliance/batchDelete', {
              ids: this.ids,
            },
            data => {
              this.$message({
                type: 'success',
                message: ''
              });
              this.getJoin();
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      handleSelectionChange(val) {
        var ids='';
        for(let i=0;i<val.length;i++){
            ids+=val[i].id+',';
        }
        this.ids=ids;
      }
    },
    created(){
      this.getJoin();
    }
  }
</script>

<style scoped>
</style>



