<template>
  <div class="body">
    <!---->
    <div class="flex" style="margin-top: 20px;">
      <div :class="['state', 'flex',membe==true?'bj':'']" @click="Xmembe()">
        <div class="state2"></div>
      </div>
      <div :class="['state', 'flex',membe==false?'bj':'']" style="margin-left: 0px" @click="Xorder()">
        <div class="state2"></div>
      </div>
    </div>
    <!---->
    <div class="head flex">
      <div class="font" style="width: 30%" v-if="membe==false"></div>
      <div class="font" style="width: 30%" v-else></div>
      <div style="width: 60%">
        <zynq></zynq>
      </div>
    </div>
    <div style="border: 1px solid #ddd;height: 600px;">
      <!---->
      <div class="tables" v-if="membe==false">
        <el-table
          ref="multipleTable"
          :data="tableData3"
          tooltip-effect="dark"
          style="width: 100%"
          border>
          <el-table-column
            label="()"
            type="index"
            align="center"
            min-width="120">
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            min-width="200">
            <template slot-scope="scope">{{ scope.row.date }}</template>
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="center"
            min-width="120">
          </el-table-column>
          <el-table-column
            prop="address"
            label=""
            min-width="120"
            align="center"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="center"
            min-width="120">
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="center"
            min-width="150">
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="center"
            min-width="150">
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <!-- -->
      <div class="tables" v-else>
        <el-table
          ref="multipleTable"
          :data="tableData3"
          tooltip-effect="dark"
          style="width: 100%"
          border>
          <el-table-column
            label=""
            type="index"
            width="100px"
            align="center">
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            min-width="180">
            <template slot-scope="scope">{{ scope.row.date }}</template>
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="center"
            min-width="200">
          </el-table-column>
          <el-table-column
            prop="address"
            label=""
            align="center"
            min-width="100"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="center"
            min-width="200">
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="center"
            min-width="200">
          </el-table-column>
        </el-table>
      </div>
      <!---->
    </div>
  </div>
</template>

<script>
  import zynq from '@/components/data/zynq'
  export default {
    name: "",
    data(){
      return{
        input:'',
        value1:'',
        value6:'',
        tableData3: [{
          date: '2016-05-03',
          name: '',
          address: ''
        }],
        membe:true
      }
    },
    components:{
      zynq,
    },
    methods: {
    //  
      Xmembe(){
        this.membe=true;
      },
      Xorder(){
        this.membe=false;
      }
    }
  }
</script>

<style scoped>
  .font{
    text-align: left;
    margin: 0px 0px 0px 30px;
    color: black;
    font-weight:700 ;
    font-size: 16px;
  }
  .tables{
    width: 90%;
    margin: 40px 0px 0px 6%;
  }
  .bj {
    background-color: rgba(0, 153, 153, 1);
    color: white;
  }
</style>



